import React from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import Home from './pages/Home'
import Trips from './pages/Trips'
import Trip from "./pages/Trip";
import Blog from "./pages/Blog";
import Post from "./pages/Post";
import Navbar from "./components/common/Navbar";
import ScrollToTop from "./helpers/ScrollToTop";

const routes = [
    {route: '/', component: Home},
    {route: '/trips', component: Trips},
    {route: '/trips/:id', component: Trip},
    {route: '/blog', component: Blog},
    {route: '/blog/:id', component: Post}
];

export default function CustomRouter () {
    var allRoutes = routes.map((route, index) => <Route exact path={route.route} component={route.component}/>);
    return (
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <ScrollToTop>
                <Navbar/>
                <Switch>
                    {allRoutes}
                </Switch>
            </ScrollToTop>
        </BrowserRouter>

    )
}
